import { makeAutoObservable, reaction, transaction, when } from "mobx";
import { IProgressionEntity, ProgressionStatus, StudyLevel } from "../interfaces/ModelInterfaces";
import { SerializedProgressionState, ISerializableEntity, TermtanglesSerialized } from "../interfaces/SerializableInterfaces"
import { parseCurriculumContainers } from "../models/CurriculumModel";
import { ProgramModel } from "../models/ProgramModel";
import { SpecialisationModel } from "../models/SpecialisationModel";
import { UserState } from "./UserState";

export class ProgressionState implements ISerializableEntity {
  constructor(
    userState: UserState
  ) {
    makeAutoObservable(this)
    this._userState = userState
    reaction(
      () => this._userState.studyLevel,
      () => {
        this._isUpdated = false;
        this._updateStudyLevel()
      },
      { fireImmediately: true }
    )
    userState.registerStudyLevelWait(async () => {
     return when(() => { return this._isUpdated; });
    });
  }

  private _userState: UserState
  private _programMapById = new Map<string, ProgramModel>();
  private _specialisationMapById = new Map<string, SpecialisationModel>();
  private _isUpdated: boolean = false;

  private async _updateStudyLevel() {
    await this.loadAllPrograms(this._userState.studyLevel);
    await this.loadAllSpecialisations(this._userState.studyLevel);
    this._isUpdated = true;
  }

  get allPrograms() {
    return [...this._programMapById.values()]
  }

  get allSpecialisations() {
    return [...this._specialisationMapById.values()]
  }

  getProgramById(id: string) {
    return this._programMapById.get(id)
  }

  getSpecialisationById(id: string) {
    return this._specialisationMapById.get(id)
  }

  get plannedPrograms(): ProgramModel[] {
    return this.allPrograms.filter(program => program.progressionStatus === ProgressionStatus.PLANNED)
  }

  get plannedSpecialisations(): SpecialisationModel[] {
    return this.allSpecialisations.filter(specialisation => specialisation.progressionStatus === ProgressionStatus.PLANNED)
  }

  get plannedProgressionEntities(): IProgressionEntity[] {
    let result: IProgressionEntity[] = this.plannedPrograms
    result = result.concat(this.plannedSpecialisations)
    return result
  }

  private resetHard(): void {
    this._programMapById.forEach((program) => {
      program.setUnplanned();
    });
    this._specialisationMapById.forEach((specialisation) => {
      specialisation.setUnplanned();
    });
  }

  addProgram(program: ProgramModel) {
    this._programMapById.set(program.id, program)
  }
  addSpecialisation(specialisation: SpecialisationModel) {
    this._specialisationMapById.set(specialisation.id, specialisation)
  }

  serialize(): SerializedProgressionState {
    return {
      programs: this.plannedPrograms.map((programModel) => {
        return programModel.serialize();
      }),
      specialisations: this.plannedSpecialisations.map((specialisationModel) => {
        return specialisationModel.serialize();
      }),
    }
  }

  deserialize(json: TermtanglesSerialized) {
    if (!json.progression) return;

    this.resetHard();
    const serializedProgressionState = json.progression;
    if (!!serializedProgressionState.programs) {
      serializedProgressionState.programs.forEach((serializedProgram) => {
        const programModel = this.getProgramById(serializedProgram.id);
        if (!!programModel) {
          programModel.deserialize(serializedProgram);
        } else {
          console.error(`ERROR: ${serializedProgram.id} is not a valid program.`);
        }
      });
    }
    if (!!serializedProgressionState.specialisations) {
      serializedProgressionState.specialisations.forEach((serializedSpecialisation) => {
        const specialisationModel = this.getSpecialisationById(serializedSpecialisation.id);
        if (!!specialisationModel) {
          specialisationModel.deserialize(serializedSpecialisation);
        } else {
          console.error(`ERROR: ${serializedSpecialisation.id} is not a valid specialisation.`);
        }
      });
    }
  
  }

  private async loadAllPrograms(studyLevel: StudyLevel) {
    return fetch(new Request(
      `https://raw.githubusercontent.com/termtangles/termtangles_data/main/publish/${studyLevel.toString()}/all_programs.json`,
      {
        method: 'GET',
        mode: 'cors',
        cache: 'default',
      }
    ))
      .then(response => response.json())
      .then(jsonData => {
        transaction(() => {
          this._programMapById.clear()
          jsonData.forEach(programObj => {
            let program = new ProgramModel(
              programObj.code,
              programObj.title,
              programObj.academicItemType,
              programObj.additionalInfo,
              programObj.creditPoints,
              programObj.structureSummary,
              parseCurriculumContainers(programObj.curriculumStructure),
              programObj.description,
              programObj.facultyName,
              programObj.implementationYear,
              programObj.intakePeriod,
              programObj.programConstraints,
              programObj.schoolName,
              programObj.studyLevel,
              programObj.urlMap
            )

            this.addProgram(program);
          })
        })
      });
  }

  private async loadAllSpecialisations(studyLevel: StudyLevel) {
    return fetch(new Request(
      `https://raw.githubusercontent.com/termtangles/termtangles_data/main/publish/${studyLevel.toString()}/all_specialisations.json`,
      {
        method: 'GET',
        mode: 'cors',
        cache: 'default',
      }
    ))
      .then(response => response.json())
      .then(jsonData => {
        transaction(() => {
          this._specialisationMapById.clear()
          jsonData.forEach(specialisationObj => {
            let specialisation = new SpecialisationModel(
              specialisationObj.code,
              specialisationObj.title,
              specialisationObj.academicItemType,
              specialisationObj.creditPoints,
              specialisationObj.structureSummary,
              parseCurriculumContainers(specialisationObj.curriculumStructure),
              specialisationObj.description,
              specialisationObj.facultyName,
              specialisationObj.implementationYear,
              specialisationObj.keywords,
              specialisationObj.schoolName,
              specialisationObj.studyLevel,
              specialisationObj.urlMap,
            )

            this.addSpecialisation(specialisation);
          })
        })
      });
  }
}

