import { makeAutoObservable } from "mobx";
import { TermModel } from "./TermModel";
import { SerializedCourse, ISerializableEntity } from "../interfaces/SerializableInterfaces"
import { ICourse, ConditionalTree, IValidatableEntity, IError } from '../interfaces/ModelInterfaces';

export enum CourseStatus {
    UNPLANNED,
    PLANNED,
    ALLOCATED,
    COMPLETED,
}

export class CourseModel implements ICourse, IValidatableEntity, ISerializableEntity {
    allocatedTerm: TermModel | undefined = undefined;
    public readonly id: string
    public courseStatus: CourseStatus = CourseStatus.UNPLANNED // Default unplanned

    private _errors: Set<IError> = new Set<IError>()

    constructor(
        public readonly code: string,
        public readonly contentTypeLabel: string,
        public readonly corequisites: string[],
        public readonly creditPoints: number,
        public readonly description: string,
        public readonly effectiveDate: Date, // Form (new Date('YYYY-MM-DD'));
        public readonly enrolmentRules: string[],
        public readonly exclusion: string[],
        public readonly facultyName: string,
        public readonly implementationYear: number,
        public readonly keywords: string[],
        public readonly prerequisites: ConditionalTree,
        public readonly schoolName: string,
        public readonly studyLevel: string,
        public readonly termOffering: string[],
        public readonly title: string,
        public readonly urlMap: string,
    ) {
        this.id = code;
        makeAutoObservable(this)
    }

    setAllocated(term: TermModel) {
        this.courseStatus = CourseStatus.ALLOCATED
        this.allocatedTerm = term
    }

    setUnplanned() {
        this._errors.clear()
        this.courseStatus = CourseStatus.UNPLANNED
        this.allocatedTerm = undefined
    }

    setCompleted() {
        this._errors.clear()
        this.courseStatus = CourseStatus.COMPLETED
        this.allocatedTerm = undefined
    }

    setPlanned() {
        this.courseStatus = CourseStatus.PLANNED
        this.allocatedTerm = undefined
    }

    //#region IValidatableEntity
    addError(error: IError) {
        this._errors.add(error)
    }

    removeError (error: IError) {
        this._errors.delete(error)
    }

    hasErrors(): boolean {
        return this._errors.size > 0
    }

    clearErrors() {
        this._errors.clear()
    }

    get errors(): IError[] {
        return [...this._errors]
    }
    //#endregion

    serialize(): SerializedCourse {
        if (this.courseStatus !== CourseStatus.ALLOCATED) {
            return {
                id: this.id,
                status: CourseStatus[this.courseStatus],
            }
        } else {
            return {
                id: this.id,
                status: CourseStatus[this.courseStatus],
                period: this.allocatedTerm?.serialize(),
            }
        }
    }

    deserialize(course: SerializedCourse) {
        if (course.id !== this.id) {
            console.error(`ERROR: This course is not ${course.id} (is: ${this.id})`);
            return;
        }

        switch(course.status) {
            case (CourseStatus[CourseStatus.PLANNED]): {
                this.setPlanned();
                break;
            }
            case (CourseStatus[CourseStatus.COMPLETED]): {
                this.setCompleted();
                break;
            }
            case (CourseStatus[CourseStatus.ALLOCATED]): {
                if (!!course.period) {
                    try {
                        let termModel = TermModel.termFromSerializedData(course.period);
                        this.setAllocated(termModel);
                    } catch (e) {
                        console.error(`ERROR: invalid term period passed in ${course.period} (${e.message})`);
                    }
                    
                } else {
                    console.error(`ERROR: Allocated course status with no period ${course.id}`);
                }
                break;
            }
            default: {
                console.error(`ERROR: ${course.status} is not a valid status`);
            }
        }
    }

    toString() {
        return `[COURSE_${this.id}] ${this.code} - ${this.title}`;
    }
}

